.TodoItem {
  padding: 1rem 0;
  margin-bottom: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #fff;
  position: relative;

  &__label {
    cursor: pointer;
    flex-basis: 82%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    &-customCheckbox {
      height: 1.6rem;
      width: 1.6rem;
      border-radius: 50%;
      margin-right: 2rem;
      border: 1px solid #fff;
      position: relative;

      &::after {
        content: '';
        display: block;
        height: 0.8rem;
        width: 0.8rem;
        border-radius: 50%;
        background: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;

        @media only sceen and (max-width: 37.5em) {
          height: 0.8rem;
          width: 0.8rem;
        }
      }
    }

    &-checkbox {
      display: none;

      &:checked + .TodoItem__label-customCheckbox,
      &:checked + .TodoItem__label-customCheckbox + .TodoItem__label-title {
        opacity: 0.8;
      }

      &:checked + .TodoItem__label-customCheckbox::after,
      &:checked
        + .TodoItem__label-customCheckbox
        + .TodoItem__label-title::after {
        opacity: 1;
      }
    }

    &-title {
      font-size: 1.7rem;
      text-transform: capitalize;
      letter-spacing: 1px;
      position: relative;

      &::after {
        content: '';
        display: inline-block;
        height: 2px;
        width: 105%;
        background: #222;
        position: absolute;
        top: 58%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
      }
    }
  }

  &__btn,
  .pen {
    cursor: pointer;
    border: none;
    color: #eee;
    background-color: transparent;
    transition: color 0.3s;

    &:not(.pen):focus {
      color: #222930;
      border: 1px dashed #222930;
    }

    &:hover {
      color: #222930;
    }
  }

  &__edit {
    width: 60%;
    display: flex;

    &-input {
      width: 100%;
      height: 2.4rem;
      border: 1px solid #182b3a;
      background-color: #182b3a;
      color: #eee;
      padding: 0 1rem;
    }

    &-btn {
      background: #fff;
      color: #222930;
      border: 1px solid #182b3a;
      width: 6rem;
    }
  }
}

.pen {
  position: absolute;
  top: 32.5%;
  right: 12%;
  font-size: 1.3rem;
  z-index: 10;

  @media only screen and (max-width: 37.5em) {
    right: 18%;
  }
}
