.App {
  text-align: center;
  color: #fff;
  background: #1b98e0;
  width: 60rem;
  margin: 15rem auto;
  padding-bottom: 3rem;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.3);

  @media only screen and (max-width: 37.5em) {
    margin: 0;
    width: 100vw;
  }

  ::selection {
    background: #092f55;
    color: #fff;
  }
}
