@import url('https://fonts.googleapis.com/css?family=Wallpoet&display=swap');

.TodoList {
  position: relative;

  @media only screen and (max-width: 37.5em) {
    height: 100vh;
    overflow: auto;
    padding-top: 7vh;
  }

  &__message {
    font-size: 1.8rem;
    padding: 2rem 0 1.2rem;

    &-cnt {
      font-family: 'Wallpoet', cursive;
      color: #182b3a;
      font-size: 2.3rem;
      font-variant-numeric: tabular-nums;
      margin: 0 0.25rem;
    }
  }

  &__form {
    display: flex;
    margin: 1rem 6rem;

    @media only screen and (max-width: 37.5em) {
      margin: 1rem 4rem;
    }

    & > * {
      border: 1px solid transparent;
    }

    & > *:focus {
      outline: none;
    }

    &-input {
      flex-basis: 80%;
      padding: 1rem 2.5rem;
      border-radius: 5rem 0 0 5rem;

      &:focus {
        box-shadow: inset 0 0.1rem 0.2rem rgba($color: #066094, $alpha: 1);
        border: 1px solid #0a8b85;
      }
    }

    &-btn {
      cursor: pointer;
      color: #fff;
      flex-basis: 20%;
      border-radius: 0 5rem 5rem 0;
      background-color: #072739;
      transition: all 0.3s;

      &:hover {
        background-color: #0b3c57;
      }

      &:focus {
        box-shadow: 0 0.1rem 0.7rem rgba($color: #000, $alpha: 0.5);
      }
    }
  }

  &__todos {
    padding: 2rem;
    margin: 0 6rem;

    @media only screen and (max-width: 37.5em) {
      margin: 0 2rem;
    }
  }

  &__clear {
    display: inline-block;
    cursor: pointer;
    border-bottom: 1px solid currentColor;
    padding-bottom: 3px;
    position: absolute;
    bottom: -7.5rem;
    left: 0;

    @media only screen and (max-width: 37.5em) {
      display: none;
    }
  }
}

.err {
  background: red;
  color: #fff;
}
