@import url(https://fonts.googleapis.com/css?family=Wallpoet&display=swap);
*,*::before,*::after{padding:0;margin:0;box-sizing:border-box}html{font-size:62.5%;background:#182b3a;background-repeat:no-repeat}@media only screen and (max-width: 37.5em){html{font-size:58%}}body{font-size:1.6rem;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.App{text-align:center;color:#fff;background:#1b98e0;width:60rem;margin:15rem auto;padding-bottom:3rem;box-shadow:0 1rem 2rem rgba(0,0,0,0.3)}@media only screen and (max-width: 37.5em){.App{margin:0;width:100vw}}.App ::-moz-selection{background:#092f55;color:#fff}.App ::selection{background:#092f55;color:#fff}

.TodoItem{padding:1rem 0;margin-bottom:1.2rem;display:flex;justify-content:space-between;align-items:center;border-bottom:1px solid #fff;position:relative}.TodoItem__label{cursor:pointer;flex-basis:82%;display:flex;justify-content:flex-start;align-items:center}.TodoItem__label-customCheckbox{height:1.6rem;width:1.6rem;border-radius:50%;margin-right:2rem;border:1px solid #fff;position:relative}.TodoItem__label-customCheckbox::after{content:'';display:block;height:0.8rem;width:0.8rem;border-radius:50%;background:#fff;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);opacity:0}@media only sceen and (max-width: 37.5em){.TodoItem__label-customCheckbox::after{height:0.8rem;width:0.8rem}}.TodoItem__label-checkbox{display:none}.TodoItem__label-checkbox:checked+.TodoItem__label-customCheckbox,.TodoItem__label-checkbox:checked+.TodoItem__label-customCheckbox+.TodoItem__label-title{opacity:0.8}.TodoItem__label-checkbox:checked+.TodoItem__label-customCheckbox::after,.TodoItem__label-checkbox:checked+.TodoItem__label-customCheckbox+.TodoItem__label-title::after{opacity:1}.TodoItem__label-title{font-size:1.7rem;text-transform:capitalize;letter-spacing:1px;position:relative}.TodoItem__label-title::after{content:'';display:inline-block;height:2px;width:105%;background:#222;position:absolute;top:58%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);opacity:0}.TodoItem__btn,.TodoItem .pen{cursor:pointer;border:none;color:#eee;background-color:transparent;-webkit-transition:color 0.3s;transition:color 0.3s}.TodoItem__btn:not(.pen):focus,.TodoItem .pen:not(.pen):focus{color:#222930;border:1px dashed #222930}.TodoItem__btn:hover,.TodoItem .pen:hover{color:#222930}.TodoItem__edit{width:60%;display:flex}.TodoItem__edit-input{width:100%;height:2.4rem;border:1px solid #182b3a;background-color:#182b3a;color:#eee;padding:0 1rem}.TodoItem__edit-btn{background:#fff;color:#222930;border:1px solid #182b3a;width:6rem}.pen{position:absolute;top:32.5%;right:12%;font-size:1.3rem;z-index:10}@media only screen and (max-width: 37.5em){.pen{right:18%}}

.TodoList{position:relative}@media only screen and (max-width: 37.5em){.TodoList{height:100vh;overflow:auto;padding-top:7vh}}.TodoList__message{font-size:1.8rem;padding:2rem 0 1.2rem}.TodoList__message-cnt{font-family:'Wallpoet', cursive;color:#182b3a;font-size:2.3rem;font-feature-settings:"tnum";font-variant-numeric:tabular-nums;margin:0 0.25rem}.TodoList__form{display:flex;margin:1rem 6rem}@media only screen and (max-width: 37.5em){.TodoList__form{margin:1rem 4rem}}.TodoList__form>*{border:1px solid transparent}.TodoList__form>*:focus{outline:none}.TodoList__form-input{flex-basis:80%;padding:1rem 2.5rem;border-radius:5rem 0 0 5rem}.TodoList__form-input:focus{box-shadow:inset 0 0.1rem 0.2rem #066094;border:1px solid #0a8b85}.TodoList__form-btn{cursor:pointer;color:#fff;flex-basis:20%;border-radius:0 5rem 5rem 0;background-color:#072739;-webkit-transition:all 0.3s;transition:all 0.3s}.TodoList__form-btn:hover{background-color:#0b3c57}.TodoList__form-btn:focus{box-shadow:0 0.1rem 0.7rem rgba(0,0,0,0.5)}.TodoList__todos{padding:2rem;margin:0 6rem}@media only screen and (max-width: 37.5em){.TodoList__todos{margin:0 2rem}}.TodoList__clear{display:inline-block;cursor:pointer;border-bottom:1px solid currentColor;padding-bottom:3px;position:absolute;bottom:-7.5rem;left:0}@media only screen and (max-width: 37.5em){.TodoList__clear{display:none}}.err{background:red;color:#fff}

